.heading {
  max-width: 68.75rem;
  margin: 0 auto;
}

.hero {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.hero__text {
  align-self: center;
  flex: 1;
}
.hero__img {
  flex: 1;
  position: relative;
  z-index: -3;
}
.hero__img::before {
  content: "";
  position: absolute;
  background-color: #e7f6ff;
  width: 110%;
  padding-bottom: 110%;
  border-radius: 50%;
  z-index: -1;
  top: -82px;
  left: -20px;
}
.hero__img::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  z-index: -2;
  background-color: #f8fcff;
  top: -80px;
  left: -120px;
  width: 145%;
  padding-bottom: 150%;
}
.hero__info {
  display: inline-block;
  color: #193e6c;
  background-color: #e7f6ff;
  font-size: 0.875rem;
  border-radius: 3.125rem;
  padding: 0.625rem 0.625rem;
  font-weight: 500;
  font-family: var(--font-jakarta);
  margin-bottom: 0.8125rem;
}
.hero__info span {
  color: #fff;
  background-color: #094b72;
  margin-right: 0.625rem;
  border-radius: 3.125rem;
  padding: 0.3125rem 1.875rem;
}
.hero__title {
  font-family: var(--font-playFair);
  font-size: 4.375rem;
  color: #094b72;
  font-weight: lighter;
  letter-spacing: 2%;
  line-height: 120%;
  margin-bottom: 2.8125rem;
}
.hero__paragraph {
  font-family: var(--font-jakarta);
  font-size: 1.25rem;
  font-weight: 500;
  letter-spacing: 0.5px;
  line-height: 128%;
  color: #4f4f4f;
  width: 75%;
  margin-bottom: 2.8125rem;
}
.hero__button {
  margin-bottom: 7rem;
  background-color: #094b72;
  display: inline-block;
  padding: 1.375rem 1.875rem;
  border-radius: 15px;
}
.hero__button a {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-family: var(--font-jakarta);
  font-size: 0.875rem;
  font-weight: 500;
}
.hero__sponsores p {
  font-size: 1rem;
  font-family: var(--font-playFair);
  color: #4f4f4f;
  margin-bottom: 2.0625rem;
}
.hero__logos {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  max-width: 30%;
  gap: 1.875rem;
}

.nav {
  display: flex;
  list-style: none;
  align-items: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  font-family: var(--font-jakarta);
  letter-spacing: 1.5px;
  margin-bottom: 2.8125rem;
}
.nav__actions {
  font-size: 0.75rem;
  text-transform: uppercase;
}
.nav__actions--middle {
  margin-left: 2.5rem;
}
.nav__actions a {
  text-decoration: none;
  color: #4f4f4f;
}
.nav__actions a .lupa {
  width: 1.875rem;
}
.nav__actions--search {
  margin-left: auto;
}
.nav__logotext {
  font-weight: 600;
  font-size: 1.25rem;
  margin-right: auto;
  text-transform: capitalize;
}
.nav__logo {
  margin-right: 0.625rem;
}

main section {
  background-color: #f2f7ff;
}

.why {
  max-width: 68.75rem;
  margin: 0 auto;
  font-family: "Playfair Display";
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4.375rem;
}
.why__grid {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  padding-top: 7.875rem;
  padding-bottom: 7.875rem;
  padding-right: 8.75rem;
  gap: 1.875rem;
}
.why__blocks {
  background-color: #fcfcfc;
  padding: 1.5rem;
  box-shadow: 0 5px 5px hsla(0deg, 0%, 0%, 0.11);
  border-radius: 5px;
}
.why__block-1 h1 {
  font-size: 2.375rem;
  padding-bottom: 0.25rem;
}
.why__block-1 p {
  font-size: 1.25rem;
}
.why__icon {
  width: 1.5625rem;
  margin-bottom: 1.5625rem;
}
.why__heading {
  margin-bottom: 0.625rem;
  font-size: 1.375rem;
}
.why__paragraph {
  font-size: 0.875rem;
  color: #4f4f4f;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --font-playFair: "Playfair Display", sans-serif;
  --font-jakarta: "Plus Jakarta Sanas", sans-serif;
}

body {
  font-size: 100%;
  font-family: "Playfair Display", "Plus Jakarta Sans", sans-serif;
}

img {
  display: block;
  width: 100%;
}